@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body{margin:0;padding:0;position:fixed;overflow:hidden;width:100%;height:100%;font-family:"Roboto",sans-serif}#root{width:100%;height:100%;background-color:#673ab7;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff;-webkit-flex-direction:column;flex-direction:column}.App{width:100vw;height:calc(100% - 50px);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}header{display:-webkit-flex;display:flex;height:50px;line-height:50px;color:#673ab7;width:100%;border-bottom:2px solid #673ab7;box-sizing:border-box;background-color:#fff}nav{display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1}.title__container{display:-webkit-flex;display:flex;-webkit-align-items:baseline;align-items:baseline}#App__title{margin:0;padding:0 .5rem;font-size:32px;font-weight:500}#App__version{font-size:14px}.edit__button{height:35px;margin:auto 1rem}#CodeContainer{background-color:#fff}#PdfContainer{background-color:#525659}.PdfContainer__initial-image{cursor:pointer;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.PdfContainer__initial-image svg{width:100%}@media screen and (max-width: 1250px){#PdfContainer,#PdfContainer__iframe{width:45vw;margin:auto}.App{-webkit-flex-direction:column;flex-direction:column}#CodeContainer,#PdfContainer{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}#CodeContainer{width:100vw}#CodeContainer,#PdfContainer,#PdfContainer__edited,#PdfContainer__iframe{height:calc(50vh - (50px / 2))}}@media screen and (max-width: calc(625px)){#PdfContainer,#PdfContainer__iframe{width:65vw}#PdfContainer{margin:auto}}@media screen and (min-width: 1251px){#CodeContainer,#PdfContainer{width:625px !important;height:950px !important;border:10px solid #673ab7}#PdfContainer__iframe{width:625px;height:950px;font-size:14px;border:none;box-sizing:border-box}}
